<template>
    <div>

        <div class="flex flex-between topline">
            <h2 class="title">Отчеты</h2>

        </div>
        <el-form>

            <el-form-item label="Дата начала:">
                <el-date-picker
                        v-model="report_start_date"
                        type="date"
                        :placeholder="this.formatDates(new Date())"
                        format="dd/MM/yyyy"
                        value-format=" dd.MM.yyyy"
                ></el-date-picker>
            </el-form-item>
            <el-form-item label="Дата окончания:">
                <el-date-picker
                        v-model="report_finish_date"
                        type="date"
                        :placeholder="this.formatDates(new Date())"
                        format="dd/MM/yyyy"
                        value-format=" dd.MM.yyyy"
                ></el-date-picker>
            </el-form-item>
            <el-form-item style="margin-top:40px;">
                <el-button
                        type="primary"
                        @click.prevent="getReportList"
                        :disabled="this.report_finish_date.length < 1 || this.report_start_date.length < 1"
                >Получить отчет
                </el-button>
            </el-form-item>
            <el-input
                    v-model="search"
                    class="search-input"
                    placeholder="Поиск по ФИО"/>
        </el-form>

        <div>
            <el-table
                    :data="reportlist.filter(data => !search || data.FIO.toLowerCase().includes(search.toLowerCase()))"
                    :default-sort = "{prop: 'END_DATE', order: 'descending'}"
                    class="history_list">
                <el-table-column sortable label="№" prop="NUMB" width="80"></el-table-column>
                <el-table-column label="ИНН" prop="INN"></el-table-column>
                <el-table-column sortable label="ФИО" prop="FIO" width="320"></el-table-column>
                <el-table-column label="Карта" prop="PAN" :formatter="formatCardNumber"></el-table-column>
                <el-table-column label="Сумма" prop="AMOUNT" width="150"></el-table-column>
                <el-table-column sortable label="Статус" prop="STATUS"></el-table-column>
                <el-table-column label="Сотрудник" prop="CUSER"></el-table-column>
                <el-table-column sortable label="Дата создания" prop="CREATE_DATE"
                                 :formatter="formatReportDate"></el-table-column>
                <el-table-column sortable label="Дата изменения" prop="END_DATE"
                                 :formatter="formatReportDate"></el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
    export default {
        components: {},
        name: "Reports",
        data() {
            return {
                reportlist: [],
                report_start_date: '',
                report_finish_date: '',
                defaultDate: this.formatDates(new Date()),
                mkk_name: this.$getCurrentMkk(),
                search: '',
            };
        },
        props: {},
        methods: {
            formatCardNumber(row, column) {
                return row.PAN.replace(/(\d)(?=(\d{4})+$)/g, '$1 ');
            },
            formatReportDate(row, column) {
                var date = row[column.property];
                return date.replace('T', ' ').slice(0, -5);
            },
            padTo2Digits(num) {
                return num.toString().padStart(2, '0');
            },
            formatDates(date) {
                return [
                    this.padTo2Digits(date.getDate()),
                    this.padTo2Digits(date.getMonth() + 1),
                    date.getFullYear(),
                ].join('.');
            },
            async getReportList() {
                try {
                    let url = "/ibs/orderReport";
                    let data = {
                        p_mkk_name: this.mkk_name,
                        p_start_date: this.report_start_date,
                        p_end_date: this.report_finish_date
                    };

                    console.log(this.report_start_date)
                    console.log(this.report_finish_date)

                    let result = await this.$axios.post(url, data);

                    if (result.status == 200 || result.status == 201) {
                        this.reportlist = JSON.parse(result.data);
                        //console.log(this.reportlist)
                    }


                } catch (e) {
                    console.log("[ERR]", e);
                }
            }

        },
        async mounted() {
            await this.getReportList();
        }
    };
</script>

<style scoped>
    h3 {
        margin: 40px 0 0;
    }

    ul {
        list-style-type: none;
        padding: 0;
    }

    li {
        display: inline-block;
        margin: 0 10px;
    }

    a {
        color: #42b983;
    }

    form {
        margin: 0;
        width: 100%;
        max-width: 100%;
    }

    .el-form {
        display: flex;
        align-items: flex-start;
    }

    .el-form-item {
        margin-right: 15px;
    }

    .history_list {
        width: 100%;
    }

    .search-input {
        width: 20%;
        margin-top: 40px;
        margin-left: auto;
    }
</style>