<template>
  <div>
    
    <div class="flex flex-between topline">
    <h2 class="title">Транзакции</h2>
    
    </div>
    <div>
          <el-table :data="transactionlist" :default-sort = "{prop: 'createdAt', order: 'descending'}" class="history_list">
          <el-table-column  label="Карта" prop="cardPan" :formatter="formatCardNumber"></el-table-column>
          <el-table-column label="Сумма" prop="amount"></el-table-column>
          <el-table-column label="Дата" prop="createdAt" :formatter="formatReportDate"></el-table-column>
        </el-table>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  name: "Transactions",
  data() {
    return {
        transactionlist: []
    };
  },
  props: {},
  methods: {
      formatCardNumber(row, column) {
          return row.cardPan.replace(/(\d)(?=(\d{4})+$)/g, '$1 ');
      },
      formatReportDate(row, column) {
          var date = row[column.property];
          return date.replace('T', ' ').slice(0, -5);
      },
async getTransactions() {
      try {
        let result = await this.$axios.get("/mkk/getTransactions");
        if (result.status == 200) {
          this.transactionlist = result.data;
          console.log(result.data);
        }
      } catch (e) {
        console.log("[ERR]", e);
      }
    },
  },
  async mounted() {
    await this.getTransactions();
  }
};
</script>

<style scoped>
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>